import React, { Component } from 'react';
import type { Props, State } from './SignUpForm.types';
import './sign-up-form.css';
import VerificationCode from '../VerificationCode';
import { Link } from 'react-router-dom';

class SignUpForm extends Component<Props, State>
{
    constructor(props: any)
    {
        super(props);
        this.state = {
            step: 0,
        }
    }

    static defaultProps = {
        role: "tenant"
    }

    prevStep ()
    {
        this.state.step > 0 ? this.setState({ step: this.state.step - 1 }) : this.props.setStep(0);
    }

    nextStep ()
    {
        this.setState({ step: this.state.step + 1 });
    }


    render ()
    {
        return (
            <>
                <div className="container-fluid p-0">
                    <div className="container-fluid" style={{ backgroundImage: 'var(--ferbend-gradient)' }}>
                        <div className='container-xl'>
                            <div className="row">
                                <div className='col-md-12'>
                                    <div className='vertical-align-parent animate__animated animate__slideInDown animate__faster'>
                                        <div className='vertical-align-child'>
                                            {
                                                this.state.step === 0 &&
                                                <>
                                                    <h3 className="text-center mt-5 mb-3">Registro</h3>
                                                </>
                                            }
                                            {
                                                this.state.step === 1 &&
                                                <>
                                                    <h3 className="text-center mt-5 mb-3">Valida tu identidad</h3>
                                                </>
                                            }
                                            {
                                                this.state.step === 2 &&
                                                <>
                                                    <h3 className="text-center mt-5 mb-3">Registro completado</h3>
                                                </>
                                            }
                                        </div>
                                    </div>
                                </div>
                                {
                                    this.state.step === 0 &&
                                    <>
                                        <div className="col-md-12">
                                            <div className='row'>
                                                <div className='col-md-6'>
                                                    <div className="form-group form-floating mb-2 animate__animated animate__fadeInRight animate__faster">
                                                        <input type="text" className="form-control" id="firstName" placeholder="Nathaniel" />
                                                        <label htmlFor="firstName">Nombres </label>
                                                    </div>
                                                </div>
                                                <div className='col-md-6'>
                                                    <div className="form-group form-floating mb-2 animate__animated animate__fadeInLeft animate__faster animate__delay-025s">
                                                        <input type="text" className="form-control" id="lastName" placeholder="Len" />
                                                        <label htmlFor="lastName">Apellidos </label>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className='row'>
                                                <div className='col-md-6'>
                                                    <div className="form-group form-floating mb-2 animate__animated animate__fadeInRight animate__faster animate__delay-05s">
                                                        <select className="form-select" id="idType">
                                                            <option value="dni">Documento Nacional de Identidad (DNI)</option>
                                                            <option value="passport">Pasaporte</option>
                                                            <option value="ce">Carn de Extranjera (CE)</option>
                                                        </select>
                                                        <label htmlFor="idType">Tipo de documento de identidad </label>
                                                    </div>
                                                </div>
                                                <div className='col-md-6'>
                                                    <div className="form-group form-floating mb-2 animate__animated animate__fadeInLeft animate__faster animate__delay-075s">
                                                        <input type="text" className="form-control" id="idNumber" placeholder="Ingrese su telfono" />
                                                        <label htmlFor="idNumber">Nmero de documento de identidad </label>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className='row'>
                                                <div className='col-md-6'>
                                                    <div className="form-group form-floating mb-2 animate__animated animate__fadeInRight animate__faster animate__delay-1s">
                                                        <input type="email" className="form-control" id="email" placeholder="example@ferbend.com" />
                                                        <label htmlFor="email">Correo electrnico </label>
                                                    </div>
                                                </div>
                                                <div className='col-md-6'>
                                                    <div className="form-group form-floating mb-2 animate__animated animate__fadeInLeft animate__faster animate__delay-125s">
                                                        <input type="tel" className="form-control" id="phone" placeholder="900000000" />
                                                        <label htmlFor="phone">Telfono</label>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className='row'>
                                                <div className='col-md-6'>
                                                    <div className="form-group form-floating mb-2 animate__animated animate__fadeInRight animate__faster animate__delay-15s">
                                                        <input type="password" className="form-control" id="contrasena" placeholder="Ingrese su contrasea" />
                                                        <label htmlFor="contrasena">Contrasea </label>
                                                    </div>
                                                </div>
                                                <div className='col-md-6'>
                                                    <div className="form-group form-floating mb-2 animate__animated animate__fadeInLeft animate__faster animate__delay-175s">
                                                        <input type="password" className="form-control" id="confirmacion-contrasena" placeholder="Confirme su contrasea" />
                                                        <label htmlFor="confirmacion-contrasena">Confirmar contrasea </label>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </>
                                }
                                {
                                    this.state.step === 1 &&
                                    <>
                                        <div className="col-md-12">
                                            <div className='row'>
                                                <div style={{ marginLeft: 'auto', marginRight: 'auto' }} className='col-md-6'>
                                                    <p className='text-center text-muted animate__animated animate__fadeInDown animate__faster'>Hemos enviado un cdigo de verificacin a example@example.com. Introduce el cdigo de verificacin para finalizar el registro</p>
                                                    <VerificationCode />
                                                </div>
                                            </div>
                                        </div>
                                    </>
                                }
                                {
                                    this.state.step === 2 &&
                                    <>
                                        <div className="col-md-12">
                                            <div className='row text-center'>
                                                <div style={{ marginLeft: 'auto', marginRight: 'auto' }} className='col-md-6'>
                                                    <svg style={{ display: 'block', margin: '0 auto 0 auto' }} xmlns="http://www.w3.org/2000/svg" width="64" height="64" fill="currentColor" className="bi bi-emoji-smile mb-3" viewBox="0 0 16 16">
                                                        <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z" />
                                                        <path d="M4.285 9.567a.5.5 0 0 1 .683.183A3.498 3.498 0 0 0 8 11.5a3.498 3.498 0 0 0 3.032-1.75.5.5 0 1 1 .866.5A4.498 4.498 0 0 1 8 12.5a4.498 4.498 0 0 1-3.898-2.25.5.5 0 0 1 .183-.683zM7 6.5C7 7.328 6.552 8 6 8s-1-.672-1-1.5S5.448 5 6 5s1 .672 1 1.5zm4 0c0 .828-.448 1.5-1 1.5s-1-.672-1-1.5S9.448 5 10 5s1 .672 1 1.5z" />
                                                    </svg>
                                                    <p className='text-muted animate__animated animate__fadeInDown animate__faster'>Tu cuenta fue validada y ya puedes iniciar sesin!</p>
                                                    <Link className='btn btn-primary mt-3' to='/'>Volver a la pgina de inicio</Link>
                                                </div>
                                            </div>
                                        </div>
                                    </>
                                }
                            </div>
                        </div>
                    </div>
                </div>


                {
                    this.state.step < 2 && <>
                        <div id="app-header">
                            <div className='header-container fixed-bottom animate__animated animate__fadeInUp'>
                            </div>
                            <nav className='header navbar fixed-bottom animate__animated animate__fadeInUp' aria-label='Opciones de navegacin'>
                                <div className='container-sm'>
                                    <button className='btn btn-outline-primary' onClick={() => this.prevStep()}>Anterior</button>
                                    <div>
                                        {
                                            this.state.step < 1 && <>
                                                <button className='btn btn-primary' onClick={() => this.nextStep()}>Siguiente</button>
                                            </>
                                        }
                                        {
                                            this.state.step === 1 && <>
                                                <button className='btn btn-primary' onClick={() => this.nextStep()}>Validar cdigo</button>
                                            </>
                                        }
                                    </div>
                                </div>
                            </nav>
                        </div>
                    </>
                }
            </>
        );
    }
}

export default SignUpForm;