import React, { Component, createRef, Fragment } from 'react';
import type { Props, State } from './Navbar.types';
import SignIn from '../../screens/Authentication/SignIn/SignIn';
import HomeFilter from './components/HomeFilter';
import PriceFilter from './components/PriceFilter';
import RoomFilter from './components/RoomFilter';
import BathroomFilter from './components/BathroomFilter';
import { Link } from 'react-router-dom';
import AreaFilter from './components/AreaFilter';
import areaFilterData from './components/AreaFilter/areaFilterOptions.json';
import areaUnitsData from './components/AreaFilter/areaUnits.json';
import bathroomFilterData from './components/BathroomFilter/bathroomFilterOptions.json';
import homeFilterData from './components/HomeFilter/homeFilterOptions.json';
import featureFilterData from './components/FeatureFilter/featureFilterOptions.json';
import priceFilterData from './components/PriceFilter/priceFilterOptions.json';
import roomFilterData from './components/RoomFilter/roomFilterOptions.json';
import Features from './components/FeatureFilter/FeatureFilter';
import './navbar.css';

class Navbar extends Component<Props, State>
{
    areaUnits: Array<{
        id: string,
        label: string,
    }>

    refMinCoveredAreaInput: any;
    refMaxCoveredAreaInput: any;
    refMinTotalAreaInput: any;
    refMaxTotalAreaInput: any;
    refAppMenu: any;
    refSearchBoxChild: any;
    refSearchBoxParent: any;
    refContainerFluid: any;

    constructor(props: any)
    {
        super(props);
        this.state = {
            showSignIn: false,
            authenticated: false,
            filters: {
                coveredAreaFilter: 0,
                coveredAreaUnit: 'm2',
                totalAreaFilter: 0,
                totalAreaUnit: 'm2',
            },
            view: 'map',
        };

        // Filtros
        this.areaUnits = [
            {
                id: 'm2',
                label: 'm2',
            },
            {
                id: 'ha',
                label: 'Ha',
            },
        ]

        // Referencias
        this.refMinCoveredAreaInput = createRef();
        this.refMaxCoveredAreaInput = createRef();
        this.refMinTotalAreaInput = createRef();
        this.refMaxTotalAreaInput = createRef();
        this.refAppMenu = createRef();
        this.refSearchBoxChild = createRef();
        this.refSearchBoxParent = createRef();
        this.refContainerFluid = createRef();
    }

    static defaultProps = {
        mode: 'home',
    }

    componentDidMount ()
    {
        if (localStorage.getItem('token'))
        {
            this.setState({ authenticated: true });
        }

        const searchBoxParent = this.refSearchBoxParent.current;
        const searchBoxChild = this.refSearchBoxChild.current;
        const containerFluid = this.refContainerFluid.current;
        const detectViewportSize = () =>
        {
            if (window.innerWidth <= 768)
            {
                if (searchBoxParent && !searchBoxParent.classList.contains('hidden'))
                {
                    searchBoxParent.classList.add('hidden');
                    searchBoxChild.classList.add();
                    containerFluid.insertBefore(searchBoxChild, containerFluid.children[1]);
                }
            } else
            {
                if (searchBoxParent && searchBoxParent.classList.contains('hidden'))
                {
                    searchBoxParent.classList.remove('hidden');
                    searchBoxParent.appendChild(searchBoxChild);
                }
            }
        }
        detectViewportSize();
        window.addEventListener('resize', detectViewportSize);
    }

    coveredAreaFilterLabel ()
    {
        let e = this.areaUnits.filter(e => { return e.id === this.state.filters.coveredAreaUnit });
        if (e.length)
        {
            return e[0].label;
        }
        return '';
    }

    totalAreaFilterLabel ()
    {
        let e = this.areaUnits.filter(e => { return e.id === this.state.filters.totalAreaUnit });
        if (e)
        {
            return e[0].label;
        }
        return '';
    }

    filter (paramI: string = '')
    {
    }

    handleFilterChange (e: any, filterName: string)
    {
        let filters = this.state.filters;
        switch (filterName)
        {
            case 'covered_area_unit':
                filters.coveredAreaUnit = e.target.dataset.id;
                break;
            default:
                break;
        }
        this.setState({ filters: filters }, () => { console.log(this.state.filters) });
    }

    handleViewChange (view: string)
    {
        this.props.handleViewChange(view);
        this.setState({
            view: view,
        })
        this.refAppMenu.current.click();
    }

    render ()
    {
        return (
            <>
                <div id='app-header'>
                    <nav className='navbar navbar-expand-md fixed-top animate__animated animate__fadeInDown' aria-label='Ferbend navigation bar'>
                        <div ref={this.refContainerFluid} className='container-fluid'>
                            <Link className='navbar-brand' aria-current='page' to='/'>
                                <div className='app-logo' />
                            </Link>
                            {
                                (this.props.mode === 'title') && <>
                                    <h3 className='my-0 py-0'>{this.props.title}</h3>
                                </>
                            }
                            <button ref={this.refAppMenu} className='navbar-toggler' type='button' data-bs-toggle='collapse'
                                data-bs-target='#appNavbar' aria-controls='appNavbar' aria-expanded='false'
                                aria-label='Toggle navigation'>
                                <span className='navbar-toggler-icon'></span>
                            </button>
                            <div className='collapse navbar-collapse' id='appNavbar'>
                                <ul className='navbar-nav me-auto mb-lg-0 gap'>
                                    {
                                        (this.props.mode === 'find') && <>
                                            <li className='nav-item'>
                                                <div className='btn-group' style={{ width: '100%' }} role='group' aria-label='Vista de inmuebles'>
                                                    <input type='radio' className='btn-check' name='toggle-view' id='map-radio' autoComplete='off' defaultChecked onClick={() => this.handleViewChange('map')} />
                                                    <label className='btn btn-outline-primary' htmlFor='map-radio'>
                                                        <svg xmlns='http://www.w3.org/2000/svg' width='16' height='16' fill='currentColor' className='bi bi-map' viewBox='0 0 16 16'>
                                                            <path fillRule='evenodd' d='M15.817.113A.5.5 0 0 1 16 .5v14a.5.5 0 0 1-.402.49l-5 1a.502.502 0 0 1-.196 0L5.5 15.01l-4.902.98A.5.5 0 0 1 0 15.5v-14a.5.5 0 0 1 .402-.49l5-1a.5.5 0 0 1 .196 0L10.5.99l4.902-.98a.5.5 0 0 1 .415.103zM10 1.91l-4-.8v12.98l4 .8V1.91zm1 12.98 4-.8V1.11l-4 .8v12.98zm-6-.8V1.11l-4 .8v12.98l4-.8z' />
                                                        </svg>
                                                    </label>
                                                    <input type='radio' className='btn-check' name='toggle-view' id='list-radio' autoComplete='off' onClick={() => this.handleViewChange('list')} />
                                                    <label className='btn btn-outline-primary' htmlFor='list-radio'>
                                                        <svg xmlns='http://www.w3.org/2000/svg' width='16' height='16' fill='currentColor' className='bi bi-view-list' viewBox='0 0 16 16'>
                                                            <path d='M3 4.5h10a2 2 0 0 1 2 2v3a2 2 0 0 1-2 2H3a2 2 0 0 1-2-2v-3a2 2 0 0 1 2-2zm0 1a1 1 0 0 0-1 1v3a1 1 0 0 0 1 1h10a1 1 0 0 0 1-1v-3a1 1 0 0 0-1-1H3zM1 2a.5.5 0 0 1 .5-.5h13a.5.5 0 0 1 0 1h-13A.5.5 0 0 1 1 2zm0 12a.5.5 0 0 1 .5-.5h13a.5.5 0 0 1 0 1h-13A.5.5 0 0 1 1 14z' />
                                                        </svg>
                                                    </label>
                                                </div>
                                            </li>
                                            <li ref={this.refSearchBoxParent} className='nav-item'>
                                                <input ref={this.refSearchBoxChild} type='search' className='form-control search-box' placeholder='Buscar' />
                                            </li>
                                            <li className='nav-item'>
                                                <div className='dropdown'>
                                                    <button className='btn btn-outline-primary dropdown-toggle' type='button' data-bs-toggle='dropdown' aria-expanded='false' data-bs-auto-close='outside'>
                                                        Tipo
                                                        {/* <svg xmlns='http://www.w3.org/2000/svg' style={{marginLeft: '.5rem', marginRight: '-0.3rem'}} width='16' height='16' fill='currentColor' className='bi bi-chevron-down' viewBox='0 0 16 16'>
                                                            <path fillRule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z' />
                                                        </svg> */}
                                                    </button>
                                                    <div className='dropdown-menu pt-0 pb-0 mx-0 mt-2 rounded-3 shadow overflow-hidden w-10'>
                                                        <div className='mx-3 my-3'>
                                                            <HomeFilter homeFilter={homeFilterData} name='homeFilter' />
                                                        </div>
                                                        <div className='p-0 border-top'>
                                                            <div className='modal-footer flex-nowrap p-0'>
                                                                <button onClick={() => this.filter('clear')} type='button' className='btn btn-lg btn-link fs-6 text-decoration-none col-6 m-0 rounded-0 border-end'
                                                                    data-bs-dismiss='modal'>Limpiar</button>
                                                                <button onClick={() => this.filter('apply')} type='button' className='btn btn-lg btn-link fs-6 text-decoration-none col-6 m-0 rounded-0'>
                                                                    <strong>Aplicar</strong></button>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </li>
                                            <li className='nav-item'>
                                                <div className='dropdown'>
                                                    <button className='btn btn-outline-primary dropdown-toggle' type='button' data-bs-toggle='dropdown' aria-expanded='false' data-bs-auto-close='outside'>
                                                        Precio
                                                        {/* <svg xmlns='http://www.w3.org/2000/svg' style={{marginLeft: '.5rem', marginRight: '-0.3rem'}} width='16' height='16' fill='currentColor' className='bi bi-chevron-down' viewBox='0 0 16 16'>
                                                            <path fillRule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z' />
                                                        </svg> */}
                                                    </button>
                                                    <div className='dropdown-menu pt-0 pb-0 mx-0 mt-2 rounded-3 shadow overflow-hidden w-15'>
                                                        <PriceFilter priceFilter={priceFilterData} />
                                                        <div className='p-0 border-top'>
                                                            <div className='modal-footer flex-nowrap p-0'>
                                                                <button onClick={() => this.filter('clear')} type='button' className='btn btn-lg btn-link fs-6 text-decoration-none col-6 m-0 rounded-0 border-end'
                                                                    data-bs-dismiss='modal'>Limpiar</button>
                                                                <button onClick={() => this.filter('apply')} type='button' className='btn btn-lg btn-link fs-6 text-decoration-none col-6 m-0 rounded-0'>
                                                                    <strong>Aplicar</strong></button>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </li>
                                            <li className='nav-item'>
                                                <div className='dropdown'>
                                                    <button className='btn btn-outline-primary dropdown-toggle' type='button' data-bs-toggle='dropdown' aria-expanded='false' data-bs-auto-close='outside'>
                                                        Habitaciones y baos
                                                        {/* <svg xmlns='http://www.w3.org/2000/svg' style={{marginLeft: '.5rem', marginRight: '-0.3rem'}} width='16' height='16' fill='currentColor' className='bi bi-chevron-down' viewBox='0 0 16 16'>
                                                            <path fillRule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z' />
                                                        </svg> */}
                                                    </button>
                                                    <div className='dropdown-menu pt-0 pb-0 mx-0 mt-2 rounded-3 shadow overflow-hidden'>
                                                        <div className='p-3 border-bottom'>
                                                            <RoomFilter roomFilter={roomFilterData} />
                                                        </div>
                                                        <div className='p-3 pb-3'>
                                                            <BathroomFilter bathroomFilter={bathroomFilterData} />
                                                        </div>
                                                        <div className='p-0 border-top'>
                                                            <div className='modal-footer flex-nowrap p-0'>
                                                                <button onClick={() => this.filter('clear')} type='button' className='btn btn-lg btn-link fs-6 text-decoration-none col-6 m-0 rounded-0 border-end'
                                                                    data-bs-dismiss='modal'>Limpiar</button>
                                                                <button onClick={() => this.filter('apply')} type='button' className='btn btn-lg btn-link fs-6 text-decoration-none col-6 m-0 rounded-0'>
                                                                    <strong>Aplicar</strong></button>
                                                            </div>
                                                        </div>

                                                    </div>
                                                </div>
                                            </li>
                                            <li className='nav-item'>
                                                <button className='btn btn-outline-primary' data-bs-toggle='offcanvas' data-bs-target='#offcanvasFilters' aria-controls='offcanvasFilters'>
                                                    <svg xmlns='http://www.w3.org/2000/svg' width='16' height='16' fill='currentColor' className='bi bi-filter' viewBox='0 0 16 16'>
                                                        <path d='M6 10.5a.5.5 0 0 1 .5-.5h3a.5.5 0 0 1 0 1h-3a.5.5 0 0 1-.5-.5zm-2-3a.5.5 0 0 1 .5-.5h7a.5.5 0 0 1 0 1h-7a.5.5 0 0 1-.5-.5zm-2-3a.5.5 0 0 1 .5-.5h11a.5.5 0 0 1 0 1h-11a.5.5 0 0 1-.5-.5z' />
                                                    </svg>
                                                </button>
                                            </li>
                                        </>
                                    }
                                </ul>
                                {
                                    !this.state.authenticated ? <>
                                        <button className='btn btn-outline-primary btn-options'
                                            onClick={() => { this.setState({ showSignIn: true }) }} title='Iniciar sesin'>
                                            <svg xmlns='http://www.w3.org/2000/svg' width='16' height='16' fill='currentColor' className='bi bi-box-arrow-in-right' viewBox='0 0 16 16'>
                                                <path fillRule='evenodd' d='M6 3.5a.5.5 0 0 1 .5-.5h8a.5.5 0 0 1 .5.5v9a.5.5 0 0 1-.5.5h-8a.5.5 0 0 1-.5-.5v-2a.5.5 0 0 0-1 0v2A1.5 1.5 0 0 0 6.5 14h8a1.5 1.5 0 0 0 1.5-1.5v-9A1.5 1.5 0 0 0 14.5 2h-8A1.5 1.5 0 0 0 5 3.5v2a.5.5 0 0 0 1 0v-2z' />
                                                <path fillRule='evenodd' d='M11.854 8.354a.5.5 0 0 0 0-.708l-3-3a.5.5 0 1 0-.708.708L10.293 7.5H1.5a.5.5 0 0 0 0 1h8.793l-2.147 2.146a.5.5 0 0 0 .708.708l3-3z' />
                                            </svg>
                                        </button>
                                    </> :
                                        <>
                                            <div className='btn-group' role='group'>
                                                <button type='button' className='btn btn-link dropdown-toggle btn-options' data-bs-toggle='dropdown' aria-expanded='false'>
                                                    <svg xmlns='http://www.w3.org/2000/svg' width='16' height='16' fill='currentColor' className='bi bi-three-dots' viewBox='0 0 16 16'>
                                                        <path d='M3 9.5a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3zm5 0a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3zm5 0a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3z' />
                                                    </svg>
                                                </button>
                                                <ul className='dropdown-menu custom-dropdown-menu dropdown-menu-options'>
                                                    {/* <li><button className='dropdown-item'>Listas</button></li> */}
                                                    <li><Link className='dropdown-item' to='/account/favorites'>Favoritos</Link></li>
                                                    <li><Link className='dropdown-item' to='/account/history'>Historial</Link></li>
                                                    {/* <li><button className='dropdown-item'>Contribuciones</button></li> */}
                                                    <li className='dropdown-divider'></li>
                                                    {/* <li><button className='dropdown-item'>Gestionar mis anuncios</button></li>
                                                    <li className='dropdown-divider'></li> */}
                                                    {/* <li><button className='dropdown-item'>Ajustes</button></li> */}
                                                    <li><button className='dropdown-item' onClick={() =>
                                                    {
                                                        localStorage.removeItem('token');
                                                        this.setState({ authenticated: false });
                                                    }
                                                    }>Cerrar sesin</button></li>
                                                </ul>
                                            </div>
                                        </>
                                }
                            </div>
                        </div>
                    </nav>
                </div>
                {
                    this.state.showSignIn && <SignIn closeSignIn={() => { this.setState({ showSignIn: false }) }}
                        afterSignIn={() =>
                        {
                            this.setState({
                                authenticated: true,
                            })
                        }}
                    ></SignIn>
                }
                <div className='offcanvas offcanvas-end' tabIndex={-1} id='offcanvasFilters' aria-labelledby='offcanvasFiltersLabel'>
                    <div className='offcanvas-header'>
                        <h5 className='offcanvas-title' id='offcanvasFiltersLabel'>Filtros</h5>
                        <button type='button' className='btn-close' data-bs-dismiss='offcanvas' aria-label='Cerrar'></button>
                    </div>
                    <div className='offcanvas-body'>
                        <div className='pb-2 border-bottom'>
                            <AreaFilter name='covered' label='rea cubierta' areaFilter={areaFilterData} areaUnits={areaUnitsData} />
                            <AreaFilter name='total' label='rea total' areaFilter={areaFilterData} areaUnits={areaUnitsData} />
                            <Features name='features' featureFilter={featureFilterData} />
                        </div>
                    </div>
                </div>
            </>
        );
    }
}

export default Navbar;