import React, { Component, createRef, Fragment } from 'react';
import type {Props, State} from './AreaFilter.types';
import Select from '../../../Elements/DropdownButton';

class AreaFilter extends Component<Props, State>
{
    refMinAreaInput: any
    refMaxAreaInput: any

    constructor(props: any)
    {
        super(props);
        this.state = {
            filters: {
                areaFilter: '',
                area: {
                    min: 0,
                    max: 0,
                },
            }
        };

        // Referencias
        this.refMinAreaInput = createRef();
        this.refMaxAreaInput = createRef();
    }

    static defaultProps = {
        label: '',
        name: '',
        areaFilter: [],
        areaUnits: []
    }

    handleFilterChange (e: any, filterName: string)
    {
        let filters = this.state.filters;
        filters.areaFilter = e.target.value;
        this.refMinAreaInput.current.value = e.target.dataset.from;
        this.refMaxAreaInput.current.value = e.target.dataset.to;
        this.setState({ filters: filters }, () => { console.log(this.state.filters) });
    }

    render ()
    {
        return (
            <>
                <h6>{this.props.label}</h6>
                <div className='pb-2 border-bottom'>
                    <div className='row gx-2'>
                        <div className='col'>
                            <input ref={this.refMinAreaInput} type='text' className='form-control' autoComplete='false' placeholder='Min' />
                        </div>
                        <div className='col'>
                            <input ref={this.refMaxAreaInput} type='text' className='form-control' autoComplete='false' placeholder='Max' />
                        </div>
                        <div className='col' style={{ flex: '0 0 1.5rem' }}>
                            <Select items={this.props.areaUnits} defaultValue='m2' />
                        </div>
                    </div>
                </div>
                <div className='my-2'>
                    {
                        this.props.areaFilter && this.props.areaFilter.map((range, i) =>
                        {
                            return (
                                <div key={i} className='form-check'>
                                    <input name={`${this.props.name}AreaFilterOption`} id={`${this.props.name}AreaFilter${i}`} className='form-check-input' type='radio' value={`${range.from}-${range.to}`} data-from={range.from} data-to={range.to} onChange={(e) => this.handleFilterChange(e, '')} />
                                    <label className='form-check-label' htmlFor={`${this.props.name}AreaFilter${i}`} >
                                        {range.label}
                                    </label>
                                </div>
                            )
                        })
                    }
                </div>
            </>
        );
    }
}

export default AreaFilter;