import React, { useEffect, useRef, useState, useCallback, useMemo } from 'react';
import { Wrapper, Status } from '@googlemaps/react-wrapper';
import { GOOGLE_MAPS_API_KEY } from '../../../../common/Maps/MapSettings';
import { v4 as uuidv4 } from 'uuid';
import './map-view.css';
import Advertisement from '../../../../common/Advertisement';
import LoadingSpinner from '../../../../common/Elements/LoadingSpinner/LoadingSpinner';
import {getTheme} from '../../../../../services/ThemeService';

const renderMap = status =>
{
    if (status === Status.LOADING) return (<>
        <div style={{ height: '100vh' }} className='vertical-align-parent animate__animated animate__slideInDown animate__faster'>
            <div className='vertical-align-child'>
                <LoadingSpinner />
            </div>
        </div>
    </>);
    if (status === Status.FAILURE) return (
        <>
            <div style={{ height: '100vh' }} className='vertical-align-parent animate__animated animate__slideInDown animate__faster'>
                <div className='vertical-align-child'>
                    <svg style={{ display: 'block', marginLeft: 'auto', marginRight: 'auto' }} xmlns="http://www.w3.org/2000/svg" width="64" height="64" fill="currentColor" className="bi bi-emoji-frown" viewBox="0 0 16 16">
                        <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z" />
                        <path d="M4.285 12.433a.5.5 0 0 0 .683-.183A3.498 3.498 0 0 1 8 10.5c1.295 0 2.426.703 3.032 1.75a.5.5 0 0 0 .866-.5A4.498 4.498 0 0 0 8 9.5a4.5 4.5 0 0 0-3.898 2.25.5.5 0 0 0 .183.683zM7 6.5C7 7.328 6.552 8 6 8s-1-.672-1-1.5S5.448 5 6 5s1 .672 1 1.5zm4 0c0 .828-.448 1.5-1 1.5s-1-.672-1-1.5S9.448 5 10 5s1 .672 1 1.5z" />
                    </svg>
                </div>
            </div>
        </>
    );
    return null;
};

function MapView ({ advertisements, center, zoom, position, className })
{
    const mapRef = useRef();
    const summaryCardContainerRef = useRef();
    const closeButtonContainerRef = useRef();
    const [map, setMap] = useState(null);
    const [summaryCard, setSummaryCard] = useState(false);
    const [summaryCardData, setSummaryCardData] = useState(null);
    const [theme, setTheme] = useState("light");
    const selectedMarkerRef = useRef(null);

    const restoreIcon = useCallback(() =>
    {
        let selectedMarker = selectedMarkerRef.current;
        if (!selectedMarker) return;
        selectedMarker.setIcon(selectedMarker.get('originalIcon'));
    }, [selectedMarkerRef]);

    const closeSummary = () =>
    {
        restoreIcon();
        setSummaryCard(false);
    };



    useMemo(() =>
    {
        advertisements.map((advertisement) =>
        {
            const coordinates = advertisement.coordinates.split(",");
            const latitude = parseFloat(coordinates[0]);
            const longitude = parseFloat(coordinates[1]);
            const originalIcon = {
                path: window.google.maps.SymbolPath.CIRCLE, // Forma del icono
                scale: 25,
                fillColor: '#6610ff',
                fillOpacity: 1,
                strokeWeight: 0,
            };

            const hoverIcon = {
                path: window.google.maps.SymbolPath.CIRCLE, // Forma del icono
                scale: 25,
                fillColor: '#fd7e14',
                fillOpacity: 1,
                strokeWeight: 0,
            };

            let marker = new window.google.maps.Marker({
                position: { lat: latitude, lng: longitude },
                label: { text: "S/" + advertisement.pricePEN, color: '#ffffff', fontSize: '0.7rem' },
                icon: originalIcon,
                originalIcon: originalIcon,
                data: { advertisement },
                map,
            });
            window.google.maps.event.addListener(marker, 'click', () =>
            {
                restoreIcon();
                marker.setIcon(hoverIcon);
                selectedMarkerRef.current = marker;
                setSummaryCardData(marker.data.advertisement);
                !summaryCard && setSummaryCard(true);
                let latitude = marker.getPosition().lat();
                let longitude = marker.getPosition().lng();
                if (window.innerWidth > 576)
                {
                    longitude += 0.01
                }
                else
                {
                    latitude -= 0.0075
                }
                const newCenter = new window.google.maps.LatLng(latitude, longitude);
                map.panTo(newCenter, { animate: true });
                map.setZoom(15);
            });
            return marker
        })
        // eslint-disable-next-line
    }, [advertisements, map]);

    useEffect(() =>
    {
        if (map)
        {
            map.setCenter(center);
            map.setZoom(zoom);
        }
    }, [map, center, zoom, position]);

    useEffect(() =>
    {
        const darkThemeMatch = window.matchMedia("(prefers-color-scheme: dark)");
        if (darkThemeMatch.matches)
        {
            setTheme("dark");
        }
        const themeChangeListener = (event) =>
        {
            if (event.matches)
            {
                setSummaryCard(false);
                setTheme("dark");
            } else
            {
                setSummaryCard(false);
                setTheme("light");
            }
        };
        darkThemeMatch.addEventListener("change", themeChangeListener);

        const mapInstance = new window.google.maps.Map(mapRef.current, {
            disableDefaultUI: true,
            styles: getTheme(theme),
        });
        const zoomInButton = document.createElement('button');
        zoomInButton.classList.add('btn');
        zoomInButton.classList.add('btn-primary');
        zoomInButton.classList.add('rounded-pill');
        zoomInButton.classList.add('btn-map-zoom');
        zoomInButton.classList.add('mt-3');
        zoomInButton.classList.add('mx-3');
        zoomInButton.innerHTML = '<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-plus-lg" viewBox="0 0 16 16"><path fill-rule="evenodd" d="M8 2a.5.5 0 0 1 .5.5v5h5a.5.5 0 0 1 0 1h-5v5a.5.5 0 0 1-1 0v-5h-5a.5.5 0 0 1 0-1h5v-5A.5.5 0 0 1 8 2Z"/></svg>';
        const zoomOutButton = document.createElement('button');
        zoomOutButton.classList.add('btn');
        zoomOutButton.classList.add('btn-primary');
        zoomOutButton.classList.add('rounded-pill');
        zoomOutButton.classList.add('btn-map-zoom');
        zoomOutButton.classList.add('mt-2');
        zoomOutButton.classList.add('mx-3');
        zoomOutButton.innerHTML = '<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-dash-lg" viewBox="0 0 16 16"><path fill-rule="evenodd" d="M2 8a.5.5 0 0 1 .5-.5h11a.5.5 0 0 1 0 1h-11A.5.5 0 0 1 2 8Z"/></svg>';
        switch (position)
        {
            case 'RIGHT_TOP':
                mapInstance.controls[window.google.maps.ControlPosition.RIGHT_TOP].push(zoomInButton);
                mapInstance.controls[window.google.maps.ControlPosition.RIGHT_TOP].push(zoomOutButton);
                break;
            case 'RIGHT_BOTTOM':
                mapInstance.controls[window.google.maps.ControlPosition.RIGHT_BOTTOM].push(zoomOutButton);
                mapInstance.controls[window.google.maps.ControlPosition.RIGHT_BOTTOM].push(zoomInButton);
                break;
            case 'LEFT_TOP':
                mapInstance.controls[window.google.maps.ControlPosition.LEFT_TOP].push(zoomInButton);
                mapInstance.controls[window.google.maps.ControlPosition.LEFT_TOP].push(zoomOutButton);
                break;
            case 'LEFT_BOTTOM':
                mapInstance.controls[window.google.maps.ControlPosition.LEFT_BOTTOM].push(zoomOutButton);
                mapInstance.controls[window.google.maps.ControlPosition.LEFT_BOTTOM].push(zoomInButton);
                break;
            default:

        }
        zoomInButton.addEventListener('click', function ()
        {
            mapInstance.setZoom(mapInstance.getZoom() + 1);
        });
        zoomOutButton.addEventListener('click', function ()
        {
            mapInstance.setZoom(mapInstance.getZoom() - 1);
        });
        setMap(mapInstance);
    }, [position, theme]);

    const onFullSize = () =>
    {
        summaryCardContainerRef.current.classList.remove('p-3');
        closeButtonContainerRef.current.classList.add('hidden');
    }

    const onRestoreSize = () =>
    {
        summaryCardContainerRef.current.classList.add('p-3');
        closeButtonContainerRef.current.classList.remove('hidden');
    }

    return (
        <>
            <div ref={mapRef} id={uuidv4()} className={className} />
            <div ref={summaryCardContainerRef} className='summary-card-container p-3'>
                {
                    summaryCard && summaryCardData && <>
                        <div ref={closeButtonContainerRef} className='btn-close-summary-container'>
                            <button type='button' className='btn-close-summary' onClick={closeSummary}>
                                <svg xmlns='http://www.w3.org/2000/svg' width='16' height='16' fill='currentColor' className='bi bi-x-lg' viewBox='0 0 16 16'>
                                    <path d='M2.146 2.854a.5.5 0 1 1 .708-.708L8 7.293l5.146-5.147a.5.5 0 0 1 .708.708L8.707 8l5.147 5.146a.5.5 0 0 1-.708.708L8 8.707l-5.146 5.147a.5.5 0 0 1-.708-.708L7.293 8 2.146 2.854Z' />
                                </svg>
                            </button>
                        </div>
                        <Advertisement fullScreen={false} content={summaryCardData} onFullSize={onFullSize} onRestoreSize={onRestoreSize} />
                    </>
                }
            </div>
        </>
    );
}
MapView.defaultProps = {
    advertisements: [],
    zoom: 10,
};
export default function Root ({ advertisements, center, zoom, position = 'RIGHT_TOP', className = '' })
{
    return (
        <>
            <Wrapper apiKey={GOOGLE_MAPS_API_KEY} render={renderMap}>
                <MapView advertisements={advertisements} center={center} zoom={zoom} position={position} className={className} />
            </Wrapper>
        </>
    );
}