import React, { Component, createRef } from 'react';
import type { Props, State } from './MagicButton.types';
import './magic-button.css'

class MagicButton extends Component<Props, State>
{
    buttonRef: any;
    constructor(props: any)
    {
        super(props);
        this.state = {
            x: 0,
            y: 0,
        }
        this.buttonRef = createRef();
    }

    static defaultProps = {
        className: 'btn btn-primary magic-btn',
    }

    mouseMove (e: any)
    {
        console.log(e.target.getBoundingClientRect());
        const rect = e.target.getBoundingClientRect();
        const xc = e.clientX - rect.left;
        const yc = e.clientY - rect.top;
        this.setState({
            x: xc,
            y: yc,
        })
    }

    render ()
    {
        const buttonStyle = {
            '--x': `${this.state.x}px`,
            '--y': `${this.state.y}px`,
        } as React.CSSProperties
        const { className, children, ...rest } = this.props;

        return (
            <>
                <button style={buttonStyle} ref={this.buttonRef} className={`${className} magic-btn`} onMouseMove={this.mouseMove.bind(this)} {...rest}>
                    {this.props.children}
                </button>
            </>
        );
    }
}

export default MagicButton;