import React, { Component } from 'react';
import type {Props, State} from './SignUp.types';
import SignUpProfile from './subcomponents/SignUpProfile';
import SignUpForm from './subcomponents/SignUpForm';
import './sign-up.css';

class SignUp extends Component<Props, State>
{
    constructor(props: any)
    {
        super(props);
        this.state = {
            step: 0,
        }
    }

    setStep (id: number)
    {
        this.setState({ step: id });
    }

    render ()
    {
        const setStepBinded = this.setStep.bind(this);
        return (
            <>
                {
                    this.state.step === 0 &&
                    <>
                        <SignUpProfile setStep={setStepBinded} />
                    </>
                }
                {

                    this.state.step === 1 &&
                    <>
                        <SignUpForm role={'tenant'} setStep={setStepBinded} />
                    </>
                }
                {

                    this.state.step === 2 &&
                    <>
                        <SignUpForm role={'individual-landlord'} setStep={setStepBinded} />
                    </>
                }
                {

                    this.state.step === 3 &&
                    <>
                        <SignUpForm role={'corporate-landlord'} setStep={setStepBinded} />
                    </>
                }
            </>
        );
    }
}

export default SignUp;