import React, { Component, Fragment } from 'react';
import type {Props, State} from './RadioButtonGroup.types';

class RadioButtonGroup extends Component<Props, State>
{
    constructor(props: any)
    {
        super(props);
        this.state = {
            filters: {
                selected: '',
            }
        };
    }

    static defaultProps = {
        name: '',
        items: []
    }

    handleFilterChange (e: any)
    {
        let filters = this.state.filters;
        filters.selected = e.target.value;
        this.setState({ filters: filters }, () => { console.log(this.state.filters) });
    }

    render ()
    {
        return (
            <>
                <div onChange={(e) => this.handleFilterChange(e)}>
                    <div className='btn-group' role='group' aria-label='Basic radio toggle button group'>
                        {
                            this.props.items && this.props.items.map((type, i) =>
                            {
                                return (
                                    <Fragment key={i}>
                                        <input id={`${this.props.name}${i}`} type='radio' className='btn-check' name={`${this.props.name}Option`} autoComplete='off' defaultChecked={i === 0} value={type.id} />
                                        <label className='btn btn-outline-primary' htmlFor={`${this.props.name}${i}`}>{type.label}</label>
                                    </Fragment>
                                )
                            })
                        }
                    </div>
                </div>
            </>
        );
    }
}

export default RadioButtonGroup;