import React, { Component } from 'react';
import type {Props, State} from './ParagraphPreview.types';
import './paragraph-preview.css'

class ParagraphPreview extends Component<Props, State>
{
    constructor(props: any)
    {
        super(props);
        this.state = {
            fullText: false,
        };
    }

    static defaultProps = {
        paragraph: '',
    }

    toggleView()
    {
        this.setState({
            fullText: !this.state.fullText
        })
    }

    render ()
    {
        return (
            <>
                <p className={`animate__animated animate__fadeInUp ${!this.state.fullText ? 'limited-text' : ''}`}>{this.props.paragraph}</p>
                <button type='button' className='btm-show-more mb-3' onClick={() => this.toggleView()}>
                    {this.state.fullText ? 'Ver menos' : 'Ver ms'}
                </button>
            </>
        );
    }
}

export default ParagraphPreview;