import React, { useEffect, useRef, useState, useMemo } from 'react';
import { Wrapper, Status } from '@googlemaps/react-wrapper';
import { HeatmapLayer } from '@deck.gl/aggregation-layers/typed';
import { GoogleMapsOverlay as DeckOverlay } from '@deck.gl/google-maps/typed';
import { GOOGLE_MAPS_API_KEY } from '../MapSettings';
import { v4 as uuidv4 } from 'uuid';
import { getTheme } from '../../../../services/ThemeService';
import citizenSecurityHeatMapData from './data/citizenSecurityHeatMap.json';
import './heat-map.css';
const DATA = citizenSecurityHeatMapData;

const renderMap = status =>
{
    if (status === Status.LOADING) return <h3>{status} ..</h3>;
    if (status === Status.FAILURE) return <h3>{status} ...</h3>;
    return null;
};

function HeatMap ({ center, zoom, position })
{
    const refMap = useRef();
    const refMarker = useRef();
    const [map, setMap] = useState(null);
    const [security, setSecurity] = useState(true);
    const [markerPosition, setMarkerPosition] = useState();
    const [theme, setTheme] = useState('light');

    const getPosition = () =>
    {
        return markerPosition;
    }

    const toggleSecurity = () => 
    {
        setSecurity(!security);
    }

    const heatmapLayer = new HeatmapLayer({
        id: 'heatmap-layer',
        data: DATA,
        getPosition: d => [d.geometry.x, d.geometry.y],
        getWeight: Math.floor(Math.random() * 10),
        radius: 500,
        intensity: 1,
        threshold: 0.05,
        opacity: .5,
    })

    const overlay = useMemo(
        () =>
            new DeckOverlay({
                layers: heatmapLayer,
            }),
        []
    );

    useEffect(() =>
    {
        security ? overlay.setMap(map) : overlay.setMap(null);
    }, [security])

    useEffect(() =>
    {
        if (map)
        {
            map.setCenter(center);
            map.setZoom(zoom);
            overlay.setMap(map);
            setMarkerPosition(center);
        }
    }, [map, center, zoom, overlay]);

    useEffect(() =>
    {
        const darkThemeMatch = window.matchMedia('(prefers-color-scheme: dark)');
        if (darkThemeMatch.matches)
        {
            setTheme('dark');
        }
        const themeChangeListener = (event) =>
        {
            if (event.matches)
            {
                setTheme('dark');
            } else
            {
                setTheme('light');
            }
        };
        darkThemeMatch.addEventListener('change', themeChangeListener);

        const mapInstance = new window.google.maps.Map(refMap.current, {
            disableDefaultUI: true,
            styles: getTheme(theme),
        });
        const zoomInButton = document.createElement('button');
        zoomInButton.classList.add('btn');
        zoomInButton.classList.add('btn-primary');
        zoomInButton.classList.add('rounded-pill');
        zoomInButton.classList.add('btn-map-zoom');
        zoomInButton.classList.add('mt-3');
        zoomInButton.classList.add('mx-3');
        zoomInButton.innerHTML = "<svg xmlns='http://www.w3.org/2000/svg' width='16' height='16' fill='currentColor' class='bi bi-plus-lg' viewBox='0 0 16 16'><path fill-rule='evenodd' d='M8 2a.5.5 0 0 1 .5.5v5h5a.5.5 0 0 1 0 1h-5v5a.5.5 0 0 1-1 0v-5h-5a.5.5 0 0 1 0-1h5v-5A.5.5 0 0 1 8 2Z'/></svg>";
        const zoomOutButton = document.createElement('button');
        zoomOutButton.classList.add('btn');
        zoomOutButton.classList.add('btn-primary');
        zoomOutButton.classList.add('rounded-pill');
        zoomOutButton.classList.add('btn-map-zoom');
        zoomOutButton.classList.add('mt-2');
        zoomOutButton.classList.add('mx-3');
        zoomOutButton.innerHTML = "<svg xmlns='http://www.w3.org/2000/svg' width='16' height='16' fill='currentColor' class='bi bi-dash-lg' viewBox='0 0 16 16'><path fill-rule='evenodd' d='M2 8a.5.5 0 0 1 .5-.5h11a.5.5 0 0 1 0 1h-11A.5.5 0 0 1 2 8Z'/></svg>";
        switch (position)
        {
            case 'RIGHT_TOP':
                mapInstance.controls[window.google.maps.ControlPosition.RIGHT_TOP].push(zoomInButton);
                mapInstance.controls[window.google.maps.ControlPosition.RIGHT_TOP].push(zoomOutButton);
                break;
            case 'RIGHT_BOTTOM':
                mapInstance.controls[window.google.maps.ControlPosition.RIGHT_BOTTOM].push(zoomOutButton);
                mapInstance.controls[window.google.maps.ControlPosition.RIGHT_BOTTOM].push(zoomInButton);
                break;
            case 'LEFT_TOP':
                mapInstance.controls[window.google.maps.ControlPosition.LEFT_TOP].push(zoomInButton);
                mapInstance.controls[window.google.maps.ControlPosition.LEFT_TOP].push(zoomOutButton);
                break;
            case 'LEFT_BOTTOM':
                mapInstance.controls[window.google.maps.ControlPosition.LEFT_BOTTOM].push(zoomOutButton);
                mapInstance.controls[window.google.maps.ControlPosition.LEFT_BOTTOM].push(zoomInButton);
                break;
            default:

        }
        zoomInButton.addEventListener('click', function ()
        {
            mapInstance.setZoom(mapInstance.getZoom() + 1);
        });
        zoomOutButton.addEventListener('click', function ()
        {
            mapInstance.setZoom(mapInstance.getZoom() - 1);
        });
        setMap(mapInstance);
    }, [position, theme]);

    useEffect(() =>
    {
        if (map)
        {
            if (refMarker.current)
            {
                refMarker.current.setMap(null);
            }
            const marker = new window.google.maps.Marker({
                position: center,
                map: map,
                draggable: true,
            });
            window.google.maps.event.addListener(marker, 'dragend', function (event)
            {
                setMarkerPosition({ lat: event.latLng.lat(), lng: event.latLng.lng() })
            });
            refMarker.current = marker;
        }
    }, [map, center]);

    return (
        <>
            {/* <button className='btn btn-primary btn-security' onClick={toggleSecurity}>
                {
                    security ? <>
                        <svg xmlns='http://www.w3.org/2000/svg' width='16' height='16' fill='currentColor' className='bi bi-shield-fill-exclamation' viewBox='0 0 16 16'>
                            <path fillRule='evenodd' d='M8 0c-.69 0-1.843.265-2.928.56-1.11.3-2.229.655-2.887.87a1.54 1.54 0 0 0-1.044 1.262c-.596 4.477.787 7.795 2.465 9.99a11.777 11.777 0 0 0 2.517 2.453c.386.273.744.482 1.048.625.28.132.581.24.829.24s.548-.108.829-.24a7.159 7.159 0 0 0 1.048-.625 11.775 11.775 0 0 0 2.517-2.453c1.678-2.195 3.061-5.513 2.465-9.99a1.541 1.541 0 0 0-1.044-1.263 62.467 62.467 0 0 0-2.887-.87C9.843.266 8.69 0 8 0zm-.55 8.502L7.1 4.995a.905.905 0 1 1 1.8 0l-.35 3.507a.552.552 0 0 1-1.1 0zM8.002 12a1 1 0 1 1 0-2 1 1 0 0 1 0 2z' />
                        </svg> </> :
                        <>
                            <svg xmlns='http://www.w3.org/2000/svg' width='16' height='16' fill='currentColor' className='bi bi-shield' viewBox='0 0 16 16'>
                                <path d='M5.338 1.59a61.44 61.44 0 0 0-2.837.856.481.481 0 0 0-.328.39c-.554 4.157.726 7.19 2.253 9.188a10.725 10.725 0 0 0 2.287 2.233c.346.244.652.42.893.533.12.057.218.095.293.118a.55.55 0 0 0 .101.025.615.615 0 0 0 .1-.025c.076-.023.174-.061.294-.118.24-.113.547-.29.893-.533a10.726 10.726 0 0 0 2.287-2.233c1.527-1.997 2.807-5.031 2.253-9.188a.48.48 0 0 0-.328-.39c-.651-.213-1.75-.56-2.837-.855C9.552 1.29 8.531 1.067 8 1.067c-.53 0-1.552.223-2.662.524zM5.072.56C6.157.265 7.31 0 8 0s1.843.265 2.928.56c1.11.3 2.229.655 2.887.87a1.54 1.54 0 0 1 1.044 1.262c.596 4.477-.787 7.795-2.465 9.99a11.775 11.775 0 0 1-2.517 2.453 7.159 7.159 0 0 1-1.048.625c-.28.132-.581.24-.829.24s-.548-.108-.829-.24a7.158 7.158 0 0 1-1.048-.625 11.777 11.777 0 0 1-2.517-2.453C1.928 10.487.545 7.169 1.141 2.692A1.54 1.54 0 0 1 2.185 1.43 62.456 62.456 0 0 1 5.072.56z' />
                            </svg>
                        </>
                }
            </button> */}
            <div ref={refMap} id={uuidv4()} className='map animate__animated animate__fadeInRight' />
        </>
    );
}

export default function Root ({ center, zoom, position = 'RIGHT_TOP' })
{
    return (
        <>
            <Wrapper apiKey={GOOGLE_MAPS_API_KEY} render={renderMap}>
                <HeatMap center={center} zoom={zoom} position={position} />
            </Wrapper>
        </>
    );
}