import React, { Component } from 'react';
import type {Props, State} from './DropdownButton.types';
import MagicButton from '../MagicButton';

class DropdownButton extends Component<Props, State>
{
    constructor(props: any)
    {
        super(props);
        this.state = {
            filters: {
                selected: this.props.defaultValue,
            },
        };
    }

    static defaultProps = {
        items: [],
        defaultValue: ''
    }

    filterLabel ()
    {
        let e = this.props.items.filter(e => { return e.id === this.state.filters.selected });
        if (e)
        {
            return e[0].label;
        }
        return '';
    }

    handleFilterChange (e: any)
    {
        let filters = this.state.filters;
        filters.selected = e.target.dataset.id;
        this.setState({ filters: filters }, () => { console.log(this.state.filters) });
    }

    render ()
    {
        return (
            <>

                <div className='dropdown'>
                    <MagicButton className='btn btn-outline-primary dropdown-toggle' type='button' data-bs-toggle='dropdown' aria-expanded='false'>
                        {this.filterLabel()}
                    </MagicButton>
                    <ul className='dropdown-menu p-2 mx-0 mt-2 rounded-3 shadow overflow-hidden'>
                        {
                            this.props.items && this.props.items.map((type, i) =>
                            {
                                return (
                                    <li key={i}>
                                        <button onClick={(e) => this.handleFilterChange(e)} data-id={type.id} className={`dropdown-item rounded-2 ${this.state.filters.selected === type.id ? 'active' : ''}`}>{type.label}</button>
                                    </li>
                                )
                            })
                        }
                    </ul>
                </div>
            </>
        );
    }
}

export default DropdownButton;