import React, { Component } from 'react';
import { Props, State } from './VerificationCode.types';
import './verification-code.css';
class VerificationCode extends Component<Props, State>
{
    focusNext (e: any)
    {
        if (e.target.value.length > 0)
            e.target.nextElementSibling.focus();
    }

    focusPrev (e: any)
    {
        if (e.keyCode === 8 && e.target.value === '')
            e.target.previousElementSibling.focus();
        if (e.target.value.length > 1)
            return false;
    }

    digit (e: any)
    {
        e.target.value = e.target.value.replace(/[^0-9]/g, '');
    }

    render ()
    {
        return (
            <>
                <div className='input-group verification-code-container'>
                    <input autoFocus className='form-control input-digit animate__animated animate__flipInY animate__fast' type='text' maxLength={1} onInput={this.digit} onKeyUp={this.focusNext} />
                    <input className='form-control input-digit animate__animated animate__flipInY animate__fast animate__fast' type='text' maxLength={1} onInput={this.digit} onKeyDown={this.focusPrev} onKeyUp={this.focusNext} />
                    <input className='form-control input-digit animate__animated animate__flipInY animate__fast' type='text' maxLength={1} onInput={this.digit} onKeyDown={this.focusPrev} onKeyUp={this.focusNext} />
                    <input className='form-control input-digit animate__animated animate__flipInY animate__fast' type='text' maxLength={1} onInput={this.digit} onKeyDown={this.focusPrev} onKeyUp={this.focusNext} />
                    <input className='form-control input-digit animate__animated animate__flipInY animate__fast' type='text' maxLength={1} onInput={this.digit} onKeyDown={this.focusPrev} onKeyUp={this.focusNext} />
                    <input className='form-control input-digit animate__animated animate__flipInY animate__fast' type='text' maxLength={1} onInput={this.digit} onKeyDown={this.focusPrev} />
                </div>
            </>
        )
    }
}
export default VerificationCode;