import React, { Component } from 'react';
import type {Props, State} from './Checks.types';

class Checks extends Component<Props, State>
{
    constructor(props: any)
    {
        super(props);
        this.state = {
            filters: {
                selected: [],
            }
        };
    }

    static defaultProps = {
        name: '',
        items: []
    }

    handleFilterChange (e: any)
    {
        let filters = this.state.filters;
        const selected = [...this.state.filters.selected];
        if (e.target.checked)
        {
            selected.push(e.target.value);
        }
        else
        {
            const index = selected.indexOf(e.target.value);
            selected.splice(index, 1);
        }
        filters.selected = selected;
        this.setState({ filters: filters }, () => { console.log(this.state.filters) });
    }

    render ()
    {
        return (
            <>
                <div>
                    {
                        this.props.items && this.props.items.map((type, i) =>
                        {
                            return (
                                <div key={i} className='form-check'>
                                    <input name={`${this.props.name}Option`} id={`${this.props.name}${i}`} className='form-check-input' type='checkbox' value={type.id} onChange={(e) => this.handleFilterChange(e)} />
                                    <label className='form-check-label' htmlFor={`${this.props.name}${i}`} >
                                        {type.label}
                                    </label>
                                </div>
                            )
                        })
                    }
                </div>
            </>
        );
    }
}

export default Checks;