import React, { Component, Fragment } from 'react';
import type { Props, State } from './NearbyPlace.types';
import './nearby-place.css';

class NearbyPlace extends Component<Props, State>
{
    static defaultProps = {
        title: 'Sin ttulo',
        data: [],
    }

    distance (lat_A: number, lng_A: number, lat_B: number, lng_B: number)
    {
        const to_radians = (degrees: number) =>
        {
            return (degrees * Math.PI) / 180;
        }
        const earth_radius = 6371;
        const d_lat = to_radians(lat_B - lat_A);
        const d_lon = to_radians(lng_B - lng_A);
        const a =
            Math.sin(d_lat / 2) * Math.sin(d_lat / 2) +
            Math.cos(to_radians(lat_A)) *
            Math.cos(to_radians(lat_B)) *
            Math.sin(d_lon / 2) *
            Math.sin(d_lon / 2);
        const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
        return (earth_radius * c).toFixed(2) + ' km';
    }

    render ()
    {
        this.props.data.results.sort((a: any, b: any) =>
        {
            if (a.rating === undefined)
                return 1;
            if (b.rating === undefined)
                return -1;
            return b.rating - a.rating;
        });
        const results = this.props.data.results.slice(0, 5);
        return (
            <>
                <h6 className="text-center">{this.props.title}</h6>
                <div className='list-group mb-3'>
                    {
                        results.map((place: any, i: number) =>
                        {
                            return <Fragment key={`data-${i}`}>
                                <div className='list-group-item list-group-item-action d-flex gap-3 py-3' aria-current='true'>
                                    <small className='flex-shrink-0 rating text-nowrap'>
                                        {
                                            place.rating ? <>
                                                {place.rating}/5
                                                <svg xmlns="http://www.w3.org/2000/svg" className="bi bi-star-fill rate-icon" viewBox="0 0 16 16">
                                                    <path d="M3.612 15.443c-.386.198-.824-.149-.746-.592l.83-4.73L.173 6.765c-.329-.314-.158-.888.283-.95l4.898-.696L7.538.792c.197-.39.73-.39.927 0l2.184 4.327 4.898.696c.441.062.612.636.282.95l-3.522 3.356.83 4.73c.078.443-.36.79-.746.592L8 13.187l-4.389 2.256z" />
                                                </svg>

                                            </> : <></>
                                        }
                                    </small>
                                    <div className='d-flex gap-2 w-100 justify-content-between'>
                                        <div>
                                            <h6 className='mb-0'>
                                                {place.name}
                                            </h6>
                                            <p className='mb-0 opacity-75'>{place.vicinity}</p>
                                        </div>
                                        <span className='text-nowrap opacity-50'>{this.distance(place.geometry.location.lat, place.geometry.location.lng, this.props.center.lat, this.props.center.lng)}</span>
                                    </div>
                                </div>
                            </Fragment>
                        })
                    }
                </div>
            </>
        );
    }
}

export default NearbyPlace;