import React, { Component, Fragment, createRef } from 'react';
import type { Props, State } from './InputNumber.types';
import { v4 as uuidv4 } from 'uuid';
import './input-number.css';

class InputNumber extends Component<Props, State>
{
    refInputNumber: any;

    constructor(props: any)
    {
        super(props);
        this.state = {
            value: this.props.defaultValue,
        };
        this.refInputNumber = createRef();
    }

    static defaultProps = {
        id: uuidv4(),
        defaultValue: 0,
        formControl: false,
    }

    increase ()
    {
        if (this.props.max !== undefined && this.refInputNumber.current.value >= this.props.max) return;
        this.refInputNumber.current.value = parseInt(this.refInputNumber.current.value) + 1;
    }

    decrease ()
    {
        if (this.props.min !== undefined && this.refInputNumber.current.value <= this.props.min) return;
        this.refInputNumber.current.value = parseInt(this.refInputNumber.current.value) - 1;
    }

    handleChange = (e: any) =>
    {
        this.setState({ value: e.target.value });
    }

    render ()
    {
        return (
            <>
                <div className={`input-group ${this.props.formControl ? 'form-control input-number-form-control' : ''}`}>
                    <span className="input-group-btn">
                        <button className="btn btn-primary btn-prev" type="button" onClick={this.decrease.bind(this)}>-</button>
                    </span>
                    <input id={this.props.id} ref={this.refInputNumber} type="number" className="form-control" value={this.state.value} onChange={this.handleChange} />
                    <span className="input-group-btn">
                        <button className="btn btn-primary btn-next" type="button" onClick={this.increase.bind(this)}>+</button>
                    </span>
                </div>
            </>
        );
    }
}

export default InputNumber;