import React, { Component } from 'react';
import type {Props, State} from './HomeFilter.types';
import MultiSelect from '../../../Elements/Checks';

class HomeFilter extends Component<Props, State>
{
    static defaultProps = {
        name: '',
        homeFilter: []
    }

    render ()
    {
        return (
            <>
                <MultiSelect items={this.props.homeFilter} name={this.props.name} />
            </>
        );
    }
}

export default HomeFilter;