import React, { Component } from 'react';
import type {Props, State} from './FeatureFilter.types';
import MultiSelect from '../../../Elements/Checks';

class FeatureFilter extends Component<Props, State>
{
    static defaultProps = {
        name: '',
        featureFilter: []
    }

    render ()
    {
        return (
            <>
                <h6>Caractersticas</h6>
                <MultiSelect items={this.props.featureFilter} name={this.props.name} />
            </>
        );
    }
}

export default FeatureFilter;