import React, { Component } from 'react';
import type { Props, State } from './Home.types';
import Navbar from '../../common/Navbar';
import MapView from './subcomponents/MapView';
import ListView from './subcomponents/ListView';
import advertisements from './data/advertisements.json';
import './home.css';

class Home extends Component<Props, State>
{
    constructor(props: any)
    {
        super(props);
        this.state = {
            view: 'map',
        }
    }

    handleViewChange(view: string) {
        if (view === this.state.view) return;
        this.setState({view: view});
    }

    render ()
    {
        return (
            <>
                <Navbar handleViewChange={this.handleViewChange.bind(this)} mode='find' />
                {
                    this.state.view === 'map' &&
                    <>
                        <div className='container-fluid p-0' style={{ height: '100vh' }}>
                            <MapView advertisements={advertisements} center={{ lat: -12.022848, lng: -77.0706962 }} zoom={12} position='LEFT_BOTTOM' className='map animate__animated animate__fadeInDown' />
                        </div>
                    </>
                }
                {
                    this.state.view === 'list' &&
                    <>
                        <ListView advertisements={advertisements} />
                    </>
                }
            </>
        );
    }
}

export default Home;