import React, { Component, createRef } from 'react';
import type {Props, State} from './PriceFilter.types';

class PriceFilter extends Component<Props, State>
{
    refMinPriceInput: any
    refMaxPriceInput: any

    constructor(props: any)
    {
        super(props);
        this.state = {
            filters: {
                priceFilter: '',
                price: {
                    min: 0,
                    max: 0,
                },
            }
        };

        // Referencias
        this.refMinPriceInput = createRef();
        this.refMaxPriceInput = createRef();
    }

    static defaultProps = {
        priceFilter: []
    }

    handleFilterChange (e: any)
    {
        let filters = this.state.filters;
        filters.priceFilter = e.target.value;
        this.refMinPriceInput.current.value = e.target.dataset.from;
        this.refMaxPriceInput.current.value = e.target.dataset.to;
        this.setState({ filters: filters }, () => { console.log(this.state.filters) });
    }

    render ()
    {
        return (
            <>
                <form className='p-2 border-bottom'>
                    <div className='row gx-2'>
                        <div className='col'>
                            <input ref={this.refMinPriceInput} type='text' className='form-control' autoComplete='false' placeholder='Min' />
                        </div>
                        <div className='col'>
                            <input ref={this.refMaxPriceInput} type='text' className='form-control' autoComplete='false' placeholder='Max' />
                        </div>
                    </div>
                </form>
                <div className='mx-2 my-2'>
                    {
                        this.props.priceFilter && this.props.priceFilter.map((range, i) =>
                        {
                            return (
                                <div key={i} className='form-check'>
                                    <input name='priceFilterOption' id={`priceFilter${i}`} className='form-check-input' type='radio' value={`${range.from}-${range.to}`} data-from={range.from} data-to={range.to} onChange={(e) => this.handleFilterChange(e)} />
                                    <label className='form-check-label' htmlFor={`priceFilter${i}`} >
                                        {range.label}
                                    </label>
                                </div>
                            )
                        })
                    }
                </div>
            </>
        );
    }
}

export default PriceFilter;