import React, { Component, createRef } from 'react';
import type { Props, State } from './ListView.types';
import Advertisement from '../../../../common/Advertisement';
import { Link } from 'react-router-dom';

class ListView extends Component<Props, State>
{
    refSelected: any
    constructor(props: any)
    {
        super(props);
        this.state = {
            selected: []
        }
        this.refSelected = []
    }
    static defaultProps = {
        advertisements: [],
    }

    toggleSelected (e: any)
    {
        const id = e.target.dataset.id;
        if (this.state.selected.includes(id))
        {
            this.setState({
                selected: this.state.selected.filter((e: any) => e !== id),
            })
        }
        else
        {
            this.setState({
                selected: [...this.state.selected, id]
            })
        }
        console.log(this.state.selected);
    }

    clearSelection ()
    {
    }
    render ()
    {
        return (
            <>
                <div style={{ height: '4rem' }}></div>
                <div className='row row-cols-1 row-cols-sm-1 row-cols-md-2 row-cols-xl-3 align-items-stretch g-3 mt-0 pb-3 px-2 mx-0 animate__animated animate__slideInUp animate__fast'>
                    {
                        this.props.advertisements.map((ad: any, i: number) =>
                        {
                            const refS = createRef<HTMLInputElement>();
                            this.refSelected.push(refS);
                            return (<div key={'ad' + i} className='col'>
                                <input ref={refS} className='form-check-input' type='checkbox' value='' aria-label='' data-id={i} onChange={this.toggleSelected.bind(this)}></input>
                                <Advertisement content={ad} />
                            </div>)
                        })
                    }
                </div>
                {
                    this.state.selected.length > 1 && <>
                        <div id="app-compare">
                            <nav className='header navbar fixed-bottom animate__animated animate__fadeInUp animate__faster' aria-label='Comparar'>
                                <div className='container-fluid'>
                                    <Link target='_blank' className='btn btn-primary me-3' to='/advertisement/compare' onClick={() => this.setState({selected: []})}>Comparar {this.state.selected.length} seleccionados</Link>
                                    <button className='btn btn-primary' onClick={this.clearSelection.bind(this)}>Limpiar seleccin</button>
                                </div>
                            </nav>
                        </div>
                    </>
                }
            </>
        );
    }
}

export default ListView;